import { Component, Inject, OnDestroy, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { IAlertConfig } from "@shared/models/alert";
import { AppAlertService } from "@shared/service/app/alert.service";
import { EmpresaService } from "@shared/service/app/empresa.service";
import { finalize, firstValueFrom, map, Observable, startWith, Subject, takeUntil, takeWhile, tap, timer } from "rxjs";
import { IVtaVentaSelect } from "../../models/venta-generica";
import { MY_DATE_FORMATS } from "@shared/constants/date-format";
import { MAT_DATE_FORMATS } from "@angular/material/core";
import { VentaGenericasService } from "../../service/venta-generica.service";
import { DatePipe } from "@angular/common";
import { MatTableDataSource } from "@angular/material/table";
import { FuseConfirmationService } from "@fuse/services/confirmation";
import { FormateadorService } from "@shared/service/app/formateador.service";

@Component({
    selector: 'app-venta-estados',
    templateUrl: './estados-ventas.component.html',
    styleUrls: ['./estados-ventas.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class VentasEstadosComponent implements OnInit, OnDestroy {
    estados: any[] = [];
    filteredEstado: Observable<any[]>; 
    form: FormGroup;
    ventas: IVtaVentaSelect;
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    columns = ['fecha', 'descripcion_estado', 'observaciones', 'acciones'];
    mostrar_tabla: boolean;
    puede_crear: boolean;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _empresaService: EmpresaService,
        private _ventaGenericaService: VentaGenericasService, 
        private datePipe: DatePipe,
        private confirm: FuseConfirmationService,
        private formateador: FormateadorService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<VentasEstadosComponent>
    ) {
        this.ventas = this.data ? this.data : null;
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
                
        // Load empresa data
        this._empresaService.getEmpresa();
        this.subscribeAlert();
        this.newForm();
        this.getEstados();
        this.getViewEstados();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                .pipe(
                    finalize(() => {
                        this.showAlert = false;
                        this._alertConfig = null;
                    }),
                    takeWhile(() => time > 0),
                    takeUntil(this._unsubscribeAll),
                    tap(() => (time -= 1))
                )
                .subscribe();
            },
        });
    }

    newForm() {
        this.form = new FormGroup({
            fecha: new FormControl(new Date(), Validators.required),
            estado: new FormControl('', Validators.required),
            observaciones: new FormControl('', Validators.required)
        })
    }

    onCargar($event?: KeyboardEvent) {
        if ($event?.key === 'Enter') {
            $event.preventDefault();
        }
        if (!this.form.valid) {
            this.form.markAllAsTouched();
            return this.alert.error('Por favor, complete todos los campos para la carga del estado de venta');
        } else {
            const fecha = this.form.get('fecha').value;
            // Se formatea la fecha nuevamente al formato deseado (YYYY-MM-DD)
            const fecha_formateada = this.datePipe.transform(fecha, 'yyyy-MM-dd');
    
            let payload = {
                idventa: this.ventas.idventa,
                idestado: this.form.get('estado').value.idestado,
                fecha: fecha_formateada,
                observaciones: this.form.get('observaciones').value
            };
    
            this._ventaGenericaService.saveEstadoVenta(payload).subscribe((resp) => {
                // Mostrar mensaje de xito
                this.alert.success(resp.mensaje);
                this.limpiarCasillero('estado');
                this.limpiarCasillero('observaciones')
                this.getViewEstados();
                this.resetFormFields();
            }, (error) => {
                // Manejo de errores
                this.alert.error('Ocurri un error al guardar el estado de venta.');
            });
        }
    }

    async getViewEstados() {
        const view_estados = await firstValueFrom(this._ventaGenericaService.getViewEstados(this.ventas.idventa));
        this.mostrar_tabla = view_estados.success;
        this.puede_crear = view_estados.puede_crear;
        this.dataSource = new MatTableDataSource(view_estados.result);
        if (view_estados.success === false) {
            this.alert.warning(view_estados.result)
            setTimeout(() => {
                this.onCloseMenu();
            }, 2000);
        } 
    }

    async getEstados(option?) {
        const Estados = await firstValueFrom(this._ventaGenericaService.getEstados(option));
        new Promise(async (resolve) => {
            this.estados = Estados.list;
    
                // // Filtrar y autoasignar el depsito por defecto
                // const depositoPorDefecto = this.depositos.find(deposito => deposito.iddeposito === this.deposito_default);
                // if (depositoPorDefecto && !this.entrega) {
                //     this.form.get('deposito_entrega').setValue(depositoPorDefecto);
                // }
    
            this.filteredEstado = this.form.get('estado').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterTextGenerico(value, this.estados, 'descripcion')),
            );
    
            resolve(true);
        })
    };

    getTextEstado(option) {
        if (option.descripcion) {
            return option.descripcion;
        }
        if (option.deposito) {
            return option.deposito;
        }
        return option;
    }

    /**
     * Funcion generica para encontrar un item en un arreglo, parametrizable.
     * @param value Valor ingresado por buscador
     * @param array Arreglo donde se compara la busqueda
     * @param param Campo por el que se desea buscar coincidencias
     * @returns Resultado de busqueda
     */
    private _filterTextGenerico(value: string, array: any[], param: string) {
        const filterValue = value.toString().toLowerCase();
        return array.filter(res => {
            const result = res[`${param}`].toString().toLowerCase().includes(filterValue);
            return result;
        });
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero(input) {
        this.form.get(input).setValue('');
    }

    resetFormFields() {
        // Especificar los campos que deseas restablecer
        const fieldsToReset = ['estado', 'observaciones'];
    
        // Iterar sobre los campos del formulario
        Object.keys(this.form.controls).forEach(key => {
            // Solo aplicar cambios a los campos especificados
            if (fieldsToReset.includes(key)) {
                this.form.controls[key].setErrors(null);
                this.form.controls[key].markAsUntouched();
                this.form.controls[key].markAsPristine();
            }
        });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteEstadoVenta(estado_venta) {
        this.confirm.open({
            title: "Eliminar Estado de Venta: N" + estado_venta.idestadoventa,
            message: `Desea eliminar el estado de venta registrado el ${this.formateador.formatDate(estado_venta.fecha)} con el estado: "${estado_venta.descripcion_estado}"?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._ventaGenericaService.deleteEstadoVenta(estado_venta.idestadoventa).subscribe({
                        next: (res) => {
                            // Manejar la respuesta exitosa
                            this.alert.success(res.mensaje);
                            this.getViewEstados();
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.warning("Este estado de venta no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    onSubmit() {
        
    }

    ngOnDestroy(): void {
        
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}